<template>
  <section>
    <div class="box">
      <div class="section__title">
        <div class="h3">My Works</div>
        <p>Here are listed the technology that will be identified by me in the development process</p>
      </div>
    </div>
    <div class="works__cases_items" v-masonry transition-duration="0.3s" item-selector=".work">
      <div v-masonry-tile v-for="(w, index) in allWorks" v-if="index < maxCases" :key="w.id" class="work">
        <router-link :to="{name: 'Work', params: {id: w.id, title: w.title}}" class="work__item">
          <div class="work__box-image">
            <img class="work__image" :src="w.imageThumbs" alt="">
          </div>
          <div class="work__button work__button_thumb">
            <h5 class="h5 work__title work__title_thumb">{{ w.title }}</h5>
            <div class="work__line"></div>
            <div class="work__button_text">view more</div>
          </div>
        </router-link>
      </div>
    </div>
    <router-link to="/works" class="btn btn__6 btn__works">view more</router-link>
  </section>
</template>

<script>
  import { mapGetters, mapActions } from 'vuex'

  export default {
    props: ['maxCases'],
    mounted () {
      this.getAllWorks()
    },
    computed: {
      ...mapGetters([
        'allWorks'
      ])
    },
    methods: {
      ...mapActions([
        'getAllWorks'
      ])
    }
  }

</script>

<style scoped lang="stylus">

.works__cases
  padding-bottom: 28px
  .section__title
    text-align center
    max-width 100%
  &_items
    margin-left: -2px
  .btn__works
    display: none

.work
  width 20%
  border-left 2px solid transparent
  border-bottom 2px solid transparent
  overflow: hidden
  cursor pointer
  &__box-image
    overflow: hidden
    position relative
    &:after
      content: ''
      position absolute
      width 100%
      height 100%
      top 0
      left 0
      background: linear-gradient(to right, #26a69a 0%, #5286a6 25%, #5286a6 50%, #8c5db4 75%, #ab48bc 100%)
      opacity: .4
      z-index 1
      transition: all .3s
      cursor pointer
  &__item
    display block
    font-size 0
  &__image
    width 100%
    transition: all .7s
  &:hover
    .work__box-image:after
      opacity 0
    .work__button
      bottom 0
  &__button
    padding 10px 5px
    position absolute
    width 100%
    bottom -82px
    transition all .6s
    background linear-gradient(to right, #26a69a 0%, #5286a6 25%, #5286a6 50%, #8c5db4 75%, #ab48bc 100%)
    &_text
      text-transform uppercase
      font-size 10px
      text-align: center
      color #eee
      font-weight: 600
  &__line
    border 1px solid #eee
    width 80%
    margin: 0 auto 10px auto
  &__title
    color #eee
    font-weight 400
    &_thumb
      text-align center

@media screen and (max-width: 1200px)
  .work
    width 25%

@media screen and (max-width: 992px)
  .work
    width 33.3%

@media screen and (max-width: 768px)
  .work
    width 50%

@media screen and (max-width: 480px)
  .work
    width 100%
    &__button
      bottom: 0
    &__box-image
      &:after
        display none
</style>



